@import url(https://fonts.googleapis.com/css2?family=Cuprum:wght@600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Sigmar+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Chewy&family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.text-title-case {
  text-transform: capitalize;
}

a.navbar-brand {
  font-family: "Sigmar One", cursive !important;
  margin-left: -3px;
  font-size: 24px;
}

.logo {
  width: 55px;
  height: 43px;
  padding-top: 0px;
}

/* Styling for the sidebar */

/* Position and sizing of burger button */
.bm-burger-button {
  position: fixed;
  width: 30px;
  height: 25px;
  right: 15px;
  top: 20px;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: #373a47;
}

/* Color/shape of burger icon bars on hover*/
.bm-burger-bars-hover {
  background: #a90000;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 30px;
  width: 54px;
}

/* Color/shape of close button cross */
.bm-cross {
  background: #bdc3c7;
}

/*
Sidebar wrapper styles
Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
*/
.bm-menu-wrap {
  position: fixed;
  height: 100%;
}

/* General sidebar styles */
.bm-menu {
  background: #373a47;
  padding: 2em 1.2em 0;
  font-size: 1.15em;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #373a47;
}

/* Wrapper for item list */
.bm-item-list {
  color: #b8b7ad;
  padding: 0.6em;
}

/* Individual item */
.bm-item {
  display: inline-block;
}

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}

/* Individual item */
.bm-item {
  display: inline-block;
  color: #d1d1d1;
  margin-bottom: 10px;
  text-align: left;
  text-decoration: none;
  transition: color 0.2s;
}

.bm-item:hover {
  color: #ffffff;
}

.nav-icon {
  color: #555;
  text-decoration: none;
  padding: 12px 0px;
  position: relative;
  display: inline-block;
  margin-left: 5px;
  margin-right: 5px;
}
.nav-icon svg {
  font-size: 25px;
}

.nav-icon:hover {
  color: grey;
}

.nav-icon .badge {
  position: absolute;
  top: 3px;
  right: -7px;
  padding: 4px 6px;
  font-size: 11px;
  border-radius: 50%;
  background-color: green;
  color: white;
}

.wrapper {
  -webkit-align-items: stretch;
          align-items: stretch;
  display: -webkit-flex;
  display: flex;
  width: 100%;
}

footer {
  min-height: 100px;
  background-color: #333;
  position: relative;
  margin-top: auto;
}

footer span {
  text-align: center;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding-bottom: 20px;
  color: white;
}

.banner-image {
  height: 400px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.banner-text {
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: white;
}

.banner-text h1 {
  font-family: "Chewy", cursive;
  font-size: 50px;
}

.banner-text p {
  font-size: 16px;
  font-family: "Montserrat", sans-serif;
}

.banner-text button {
  font-family: "Chewy", cursive;
  font-weight: 20px;
  border: none;
  outline: 0;
  display: inline-block;
  padding: 10px 29px;
  color: #ddd;
  background-color: #009879;
  text-align: center;
  border-radius: 20px;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  margin: 15px;
}

.banner-text button:hover {
  color: white;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.container {
  padding: 30px;
}

/* Profile Image Upload Styling */
#profile-img-input {
  display: none;
}

#profile-container {
  position: relative;
  width: 120px;
  height: 120px;
  overflow: hidden;
  border-radius: 50%;
  transition: -webkit-filter 0.8s ease;
  transition: filter 0.8s ease;
  transition: filter 0.8s ease, -webkit-filter 0.8s ease;
}

#profile-container img {
  width: 120px;
  height: 120px;
  object-fit: cover;
  opacity: 1;
  transition: -webkit-filter 0.5s ease;
  transition: filter 0.5s ease;
  transition: filter 0.5s ease, -webkit-filter 0.5s ease;
}

#profile-container .icon-container {
  transition: 0.5s ease;
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  text-align: center;
}

#profile-container:hover {
  -webkit-filter: drop-shadow(1px 1px 22px #7584bb);
          filter: drop-shadow(1px 1px 22px #7584bb);
  cursor: pointer;
}

#profile-container:hover img {
  opacity: 0.7;
}

#profile-container:hover .icon-container {
  opacity: 1;
}

.icon-container .camera-icon {
  background-color: darkgray;
  color: white;
  border-radius: 50%;
  font-size: 16px;
  padding: 16px 16px;
}

.main {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  min-height: 100vh;
  min-width: 0;
  transition: margin-left 0.35s ease-in-out, left 0.35s ease-in-out;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.content {
  padding: 2.5rem 2.5rem 1.5rem;
  -webkit-flex: 1 1;
          flex: 1 1;
}

@media screen and (max-width: 1199.98px) {
  .content {
    padding: 1.5rem;
  }
}

@media screen and (max-width: 991.98px) {
  .content {
    width: 100vw;
    max-width: 100vw;
  }
}

